.body {
  height: 100%;
  overflow-y: scroll;
}
.listContainer {
  width: 100%;
  height: 98%;
}

.elementsLine {
  width: 96%;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  line-height: 30px;
  border-bottom: 1px dashed #ccc;
}

.tableHeader {
  font-size: 16px;
  border-bottom: 1px none;
  margin-bottom: 15px;
}
.tableHeader div:first-letter {
  text-transform: uppercase;
}
